* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

body {
  color: #333;
  background-color: #f7f9fc;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  display: flex;
}

h1 {
  color: #333;
  margin-bottom: 10px;
  font-size: 2em;
}

p {
  font-size: 1em;
}

hr {
  color: #537;
  margin-top: 5px;
}

#drop-area {
  color: #537;
  text-align: center;
  background-color: #f6f0fc;
  border: 2px dashed #537;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 200px;
  margin-top: 20px;
  font-size: 1.1em;
  transition: background-color .3s;
  display: flex;
}

#drop-area:hover {
  background-color: #fcf9f0;
}

.header {
  text-align: center;
}

.wrapper {
  width: 600px;
}

.file-counter {
  text-align: center;
  background-color: #6cc5e9;
  border-radius: 5px;
  margin: 20px 0;
  padding: 10px 20px;
}

.file-counter p {
  color: #391a55;
  font-weight: 800;
}

.form-heading {
  margin-top: 15px;
  font-size: 1.2rem;
  font-weight: 800;
}

.resolution-select {
  flex-direction: column;
  justify-content: center;
  width: 100%;
  margin-bottom: 20px;
  display: flex;
}

.resolution-select label {
  color: #333;
  margin-top: 10px;
  font-size: 1em;
}

.resolution-select .custom-resolution {
  place-content: center space-between;
  margin-top: 10px;
  display: flex;
}

.custom-resolution span {
  align-self: center;
  font-size: 2rem;
}

.custom-resolution div {
  max-width: 43%;
}

#apply-resolution {
  margin-top: 10px;
}

.field-info {
  color: #555;
  margin-top: 7px;
  font-size: .9rem;
}

.field-info-bottom {
  color: #555;
  font-size: .9rem;
}

select, input[type="number"] {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  margin-top: 5px;
  margin-bottom: 5px;
  padding: 10px;
  font-size: 1em;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #537;
  border: none;
  border-radius: 50px;
  padding: 15px 25px;
  font-size: 1em;
  font-weight: 800;
  transition: background-color .3s;
}

button:hover {
  color: #391a55;
  background-color: #ea1;
}

button:hover svg {
  fill: #391a55 !important;
}

button:disabled {
  cursor: not-allowed;
  background-color: #b0d4ff;
}

#preview-list {
  word-break: break-all;
  width: 100%;
  margin-bottom: 20px;
  list-style: none;
}

#preview-list li {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  align-items: center;
  margin-bottom: 10px;
  padding: 10px;
  display: flex;
}

#preview-list li img {
  max-width: 150px;
  max-height: 150px;
}

#preview-list div {
  margin: 0 10px;
}

#preview-list .download-button {
  margin-left: auto;
  padding: 10px 20px;
}

#preview-list .download-button svg {
  fill: #fff;
  transition: fill .3s;
}

#preview-list .image-name {
  margin-bottom: 10px;
  margin-left: auto;
}

#preview-list li:first-child {
  border-top: 0;
  border-radius: 0 0 5px 5px;
}

.download-all {
  width: 100%;
}

.loader {
  border: 6px solid #f3f3f3;
  border-top-color: #537;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin: 20px auto;
  animation: 1s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.hidden, .resolution-select.hidden {
  display: none;
}

@media screen and (width <= 600px) {
  .wrapper {
    width: 100%;
  }
}
